import { useLayoutEffect, useState } from 'react';
import styled from '@emotion/styled';
import { StaticImage } from 'gatsby-plugin-image';

function getRandomNumber() {
  return Math.floor(Math.random() * 6);
}

export default function BackgroundImage() {
  const [imageIndex, setImageIndex] = useState(Number.MIN_SAFE_INTEGER);

  useLayoutEffect(() => {
    const randomNumber = getRandomNumber();
    setImageIndex(randomNumber);
  }, []);

  return (
    <HeaderBackground>
      {imageIndex === 0 && (
        <StaticImage
          src="../../content/img/common/back0.jpg"
          alt="header background"
          placeholder="blurred"
          layout="fullWidth"
        />
      )}
      {imageIndex === 1 && (
        <StaticImage
          src="../../content/img/common/back1.jpg"
          alt="header background"
          placeholder="blurred"
          layout="fullWidth"
        />
      )}
      {imageIndex === 2 && (
        <StaticImage
          src="../../content/img/common/back2.jpg"
          alt="header background"
          placeholder="blurred"
          layout="fullWidth"
        />
      )}
      {imageIndex === 3 && (
        <StaticImage
          src="../../content/img/common/back3.jpg"
          alt="header background"
          placeholder="blurred"
          layout="fullWidth"
        />
      )}
      {imageIndex === 4 && (
        <StaticImage
          src="../../content/img/common/back4.jpg"
          alt="header background"
          placeholder="blurred"
          layout="fullWidth"
        />
      )}
      {imageIndex === 5 && (
        <StaticImage
          src="../../content/img/common/back5.jpg"
          alt="header background"
          placeholder="blurred"
          layout="fullWidth"
        />
      )}
    </HeaderBackground>
  );
}

const HeaderBackground = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 68rem;

  img {
    width: 100%;
    height: 68rem;
  }

  div[data-gatsby-image-wrapper] {
    height: 68rem;
  }

  @media (max-width: 780px) {
    height: 40rem;

    img {
      width: 100%;
      height: 40rem;
    }

    div[data-gatsby-image-wrapper] {
      height: 40rem;
    }
  }
`;
