import { useCallback, useContext, useEffect, useRef, useState } from 'react';
import { Link } from 'gatsby';
import { css } from '@emotion/react';
import styled from '@emotion/styled';

import SiteNavLogo from '@/components/header/SiteNavLogo.tsx';
import ThemeModeSwitch from '@/components/header/ThemeModeSwitch.tsx';
import { Github } from '@/components/icons/github.tsx';
import { Instagram } from '@/components/icons/instagram.tsx';
import { ThemeToggleContext } from '@/contexts/ThemeToggleContext.ts';
import { inner, SocialLink, SocialLinkFb } from '@/styles/shared.ts';
import config from '@/website-config.ts';

const HEIGHT_OFFSET = 92;

export default function HomeSiteNav() {
  const [isScrollDown, isSetScrollDown] = useState(false);
  const lastScrollY = useRef<number>(0);
  const ticking = useRef<boolean>(false);
  const { theme } = useContext(ThemeToggleContext);
  const isWhite = !(isScrollDown && theme === 'light');

  const onScroll = useCallback(() => {
    if (!ticking.current) {
      requestAnimationFrame(() => {
        lastScrollY.current = window.scrollY;
        const isScrolled = lastScrollY.current >= HEIGHT_OFFSET;
        isSetScrollDown(isScrolled);
        ticking.current = false;
      });
    }
    ticking.current = true;
  }, []);

  useEffect(() => {
    onScroll();
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => window.removeEventListener('scroll', onScroll);
  }, [onScroll]);

  return (
    <header className="site-header">
      <div css={[SiteNavMain]} className={`${isScrollDown ? 'is-scroll-down' : ''}`}>
        <div css={inner}>
          <nav css={SiteNavStyles}>
            <SiteNavLeft className={`site-nav-left`}>
              <SiteNavLogo />
              <SiteNavContent>
                <ul css={NavStyles} role="menu">
                  <li role="menuitem">
                    <Link
                      to="/"
                      activeClassName="nav-current"
                      className={isWhite ? 'is-white' : ''}
                    >
                      Home
                    </Link>
                  </li>
                  <li role="menuitem">
                    <Link
                      to="/about"
                      activeClassName="nav-current"
                      className={isWhite ? 'is-white' : ''}
                    >
                      About
                    </Link>
                  </li>
                  <li role="menuitem">
                    <Link
                      to="/tags"
                      activeClassName="nav-current"
                      className={isWhite ? 'is-white' : ''}
                    >
                      Tags
                    </Link>
                  </li>
                  <li role="menuitem">
                    <Link
                      to="/tags/diary/"
                      activeClassName="nav-current"
                      className={isWhite ? 'is-white' : ''}
                    >
                      Diary
                    </Link>
                  </li>
                </ul>
              </SiteNavContent>
            </SiteNavLeft>
            <SiteNavRight>
              <SocialLinks>
                {config.instagram && (
                  <a
                    className={`not-mobile ${isWhite ? 'is-white' : ''}`}
                    css={[SocialLink, SocialLinkFb]}
                    href={config.instagram}
                    target="_blank"
                    title="Instagram"
                    rel="noopener noreferrer"
                  >
                    <Instagram />
                  </a>
                )}
                {config.github && (
                  <a
                    className={`not-mobile ${isWhite ? 'is-white' : ''}`}
                    css={SocialLink}
                    href={config.github}
                    title="Github"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <Github />
                  </a>
                )}

                <ThemeModeSwitch isWhite={isWhite} />
              </SocialLinks>
            </SiteNavRight>
          </nav>
        </div>
      </div>
    </header>
  );
}

export const SiteNavMain = css`
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  left: 0;
  padding: 0 5vw;
  transition: background 0.45s ease;

  &.is-scroll-down {
    background: var(--nav-background);
    backdrop-filter: saturate(180%) blur(20px);
    border-bottom: 1px solid var(--nav-border-color);
  }
`;

const SiteNavStyles = css`
  position: relative;
  z-index: 100;
  overflow-y: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 52px;
  font-size: 1.3rem;
`;

const SiteNavLeft = styled.div`
  overflow: hidden;
  display: flex;
  flex: 1 0 auto;
  align-items: center;
  margin-right: 10px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.2px;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;

  -ms-overflow-scrolling: touch;

  @media (max-width: 700px) {
    margin-right: 0;
  }
`;

const SiteNavContent = styled.div`
  position: relative;
  align-self: flex-start;
`;

const NavStyles = css`
  position: absolute;
  z-index: 1000;
  display: flex;
  margin: 0 0 0 -12px;
  padding: 0;
  list-style: none;
  transition: all 1s cubic-bezier(0.19, 1, 0.22, 1);

  li {
    display: block;
    margin: 0;
    padding: 0;
  }

  li a {
    position: relative;
    display: block;
    padding: 10px 12px;
    font-size: 1.2rem;
    color: var(--main-color);
    opacity: 0.7;
    transition: opacity 0.2s ease-in-out;

    @media (max-width: 700px) {
      padding: 10px 8px;
    }
  }

  li a:hover {
    text-decoration: none;
    opacity: 1;
  }

  li a.is-white {
    color: #fff;

    &:before {
      background: #fff;
    }
  }

  .nav-current {
    opacity: 1;
  }
`;

const SiteNavRight = styled.div`
  display: flex;
  flex: 0 1 auto;
  align-items: center;
  justify-content: flex-end;
`;

const SocialLinks = styled.div`
  display: flex;
  flex-shrink: 0;
  align-items: center;

  @media (max-width: 700px) {
    .not-mobile {
      display: none;
    }
  }
`;
